  h1 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  
  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .download-cv {
    background-color: #007bff;
    box-shadow: #222;
    color: #fff;
    box-shadow: rgb(16, 12, 12);
    box-shadow: 0 15px 6px 0 rgba(250, 55, 55, 0.09);
  }
  
  .get-in-touch {
    background-color: #28a745;
    color: #fff;
    box-shadow: rgb(16, 12, 12);
    box-shadow: 0 15px 6px 0 rgba(250, 55, 55, 0.09);
  }
